html, body {
    width: 100%;
    margin: 0;
    padding: 0; }

body {
    background: #203b4e url("/img/body-bg.jpg") no-repeat;
    display: flex;
    height: 100%;
    min-width: 300px; }


.wrapper {
    flex: 1;
    width: 100%;
    height: 100%; }


.popup {
    position: fixed;
    .menu-btn {
        position: relative; } }
ul {
    list-style: none; }

h1,h2,h3,h4,h5 {
    margin-top: 0;
    margin-bottom: 0;
    font-weight: normal; }
p, ul, li {
    padding: 0;
    margin: 0; }
a {
    text-decoration: none; }
textarea {
    resize: none; }
div, input, textarea, select, button, span, a:active {
    outline: none; }
.main-content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    background: url(/img/sidebar-bg.jpg); }

.page-main {
    flex: 3;
    color: #373737;
    background: #fff;
    &__wrapper {}
    display: flex;
    flex-direction: column;
    height: 100%;
    section {
        flex: 1; } }


.icon {
    background: url(../img/sprite.png);
    display: inline-block;
    &_lift {
        background-position: -468px -59px;
        width: 20px;
        height: 20px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); }
    &_advantages-01 {
        background-position: -78px -290px;
        width: 74px;
        height: 74px; }
    &_advantages-02 {
        background-position: -230px -290px;
        width: 74px;
        height: 74px; }
    &_advantages-03 {
        background-position: -154px -290px;
        width: 74px;
        height: 74px; }
    &_mail {
        background-position: -468px -193px;
        width: 18px;
        height: 14px;
        &-blue {
            background-position: -468px -153px;
            width: 20px;
            height: 16px; } }
    &_adress {
        background-position: -468px -171px;
        width: 14px;
        height: 20px; }
    &_phone {
        background-position: -468px -105px;
        width: 15px;
        height: 25px;
        &-blue {
            background-position: -468px -132px;
            width: 19px;
            height: 19px; } } }

.content {
    &__text {
        padding: 0 20px;
        line-height: 1.6; }
    &__header {
        background: #eeeefb;
        color: #373737;
        text-align: center;
        padding: 25px 0 25px 0;
        margin-bottom: 20px;
        font-size: 36px;
        font-weight: bold; } }
